
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { NavLink } from 'react-router-dom';
import { authAPI, profileAPI } from '../../api/api';
import { getUserDataTC, logOutTC, setUserData } from '../../redux/authReducer';
import Avatar from '../common/Avatar';
import classes from './Header.module.scss';


const Header = (props) => {
    return(
        <div className={classes.header}>
            <div className={classes.header__logo}>My App</div>
            <div className={classes.header__login}>
                <Avatar size={'small'}/>
                <div>{props.isAuth ? <div><NavLink to={'/profile'}>{props.login}</NavLink>  <div className={classes.logout} onClick={props.logOut}>Log Out</div></div> : <div> <div className={classes.unknown}>Unknown User</div>  <NavLink to={'/login'}>Log In</NavLink></div>}</div>
            </div>
        </div>
    )
}

class HeaderContainer extends Component{

    // componentDidMount=()=>{
    //    this.props.getUserDataTC()
    // }

    render(){
        return(
            <Header {...this.props}/>
        )
    }
}

let mapStateToProps = (state) => {
    return{
        isAuth: state.auth.isAuth,
        login: state.auth.login
    }
}



export default connect(mapStateToProps,{setUserData, getUserDataTC, logOut: logOutTC })(HeaderContainer);