import { stopSubmit } from "redux-form";
import { authAPI } from "../api/api"

// state

let initState = {
    userId: null,
    email: null,
    login: null,
    isAuth: false
}

// actions

let SET_USER_DATA = 'SET_USER_DATA';

export const setUserData = (userId, email, login, isAuth) => {return{type: SET_USER_DATA, payload:{userId, email, login, isAuth}}}

// reducer

const authReducer = (state=initState, action) =>{
    switch(action.type){
        case SET_USER_DATA: {
            return {...state, ...action.payload }
        }
        default: return state
    }
}

//thunks

export const getUserDataTC = () => {return(dispatch) =>{
    authAPI.me()
    .then(response=>{
        if  (response.data.resultCode === 0){
        let {id, email, login} = response.data.data;
        dispatch(setUserData(id, email, login, true));
    }})
}}


export const logInTC = (email, password, rememberMe) => {return(dispatch)=>{
    authAPI.login(email, password, rememberMe)
    .then(response=>{
        if(response.data.resultCode === 0){
            dispatch(getUserDataTC());
        } else {
            let message = response.data.messages.length > 0 ? response.data.messages[0] : 'Unknown error';
            dispatch(stopSubmit('login', {_error: message}))
        }
    })
}}


export const logOutTC = () => {return(dispatch)=>{
    authAPI.logout().then(response=>{
        if(response.data.resultCode === 0){
            dispatch(setUserData(null, null, null, false));
        }
    })
}}




export default authReducer;