import * as axios from "axios";

const createdAxios = axios.create({
    baseURL: `https://social-network.samuraijs.com/api/1.0/`,
    withCredentials: true,
    headers:{
        'API-KEY': 'cd8fc3f1-bf2d-448b-b9c6-467846b31684'
    }
});



export const usersAPI = {

    getUsers(currentPage = 1, pageSize = 100){   
        return createdAxios
         .get(`users?page=${currentPage}&count=${pageSize}`)
         .then(response => response);
    },
    followUser(userId){
        return createdAxios.post( `follow/${userId}` )
    },
    unfollowUser(userId){
        return createdAxios.delete( `follow/${userId}` )
    }
}

export const profileAPI = {
    getProfile(userId){
        return createdAxios.get(`profile/${userId}`)
    },
    getUserStatus(userId){
        return createdAxios.get(`profile/status/${userId}`)
    },
    updateStatus(status){
        return createdAxios.put(`profile/status`, {status})
    }
}

export const authAPI = {
    me(){
        return createdAxios.get(`auth/me`)
    },
    login(email, password, rememberMe = false){
        return createdAxios.post(`auth/login`, {email, password, rememberMe})
    },
    logout(){
        return createdAxios.delete(`auth/login`)
    }
}

