import React, { Component } from 'react';
import classes from './Profile.module.scss';
import facebook from './../../assets/icons/facebook.svg';
import instagram from './../../assets/icons/instagram.svg';
import twitter from './../../assets/icons/twitter.svg';
import youtube from './../../assets/icons/youtube.svg';
import Avatar from '../common/Avatar';
import { getProfileTC, getStatusTC, updateStatusTC, addPost } from '../../redux/profileReducer';
import { connect } from 'react-redux';
import Loader from '../common/Loader';
import { withRouter } from 'react-router-dom';
import withAuthRedirect from '../../hoc/AuthRedirect';
import { compose } from 'redux';
import ProfileStatus from './ProfileStatus';
import MyPosts from './MyPosts/MyPosts';
import AddNewPostReduxForm from './MyPosts/AddNewPostForm';




const ProfileCard = (props) => {
    if( !props.profile){
        return <Loader/>
    }
    return(
    <div className={classes.profileCard}>
        
            <div className={classes.profileCard__header}>
                <Avatar img={props.profile.photos.small} size={'big'}/>
            </div>
            <div className={classes.profileCard__body}>
                <div className={classes.profileCard__name}>{props.profile.fullName}</div>
                <div className={classes.profileCard__status}>
                    <ProfileStatus status={props.status} updateStatus={props.updateStatus}/>
                </div>
                <div className={classes.profileCard__social}>
                    <div className={classes.profileCard__socialIcon}>
                        <img src={youtube}/>
                    </div>
                    <div className={classes.profileCard__socialIcon}>
                        <img src={instagram}/>
                    </div>
                    <div className={classes.profileCard__socialIcon}>
                        <img src={twitter}/>
                    </div>
                    <div className={classes.profileCard__socialIcon}>
                    <img src={facebook}/>
                    </div>
                </div>
            </div>
            <div className={classes.profileCard__footer}>
                <div className={classes.profileCard__btn}>
                    <button disabled='true'>Follow</button>
                </div>
            </div>
        </div>
        
    
    )
}

const Profile = (props) => {

    let addPost = (values)=>{
        props.addPost(values.newPostText);
    }
    
    return(
        <div>
            <ProfileCard profile={props.profile} status={props.status} updateStatus={props.updateStatus}/>
            <AddNewPostReduxForm onSubmit={addPost}/>
            <MyPosts posts={props.posts} profile={props.profile}/>
        </div>
    )
}

class ProfileContainer extends Component{

    componentDidMount = () => {
        let userId = this.props.match.params.userId;
        if(!userId){userId = this.props.authUserId}
        this.props.setProfile(userId);
        this.props.setStatus(userId);
    }


    render(){
        return(
            <Profile {...this.props} 
            updateStatus={this.props.updateStatus} 
            status={this.props.status} 
            profile={this.props.profile} 
            setProfile={this.props.setProfile}
            posts={this.props.posts}/>
        )
    }
}

let mapStateToProps = (state) => {
    return{
        profile: state.profilePage.profile,
        status: state.profilePage.status,
        posts: state.profilePage.posts,
        authUserId: state.auth.userId
    }
}



export default compose(connect(mapStateToProps,{setProfile: getProfileTC, setStatus: getStatusTC, updateStatus: updateStatusTC, addPost}), withAuthRedirect, withRouter)(ProfileContainer);