import React, { useState } from 'react';
import classes from './Profile.module.scss';

const ProfileStatus = (props) => {

    let [editMode, setEditMode] = useState(false);
    let [status, setStatus] = useState(props.status);

    let activateEditMode = () =>{
        setEditMode(true)
    }



    const deactivateEditMode = () =>{
        setEditMode(false)
        props.updateStatus(status)
    }


    let statusChange = (e) => {
        setStatus(e.currentTarget.value);
    }


    return(
        <div>
            {editMode ?
            <input className={classes.profileCard__status_input} onChange={statusChange} onBlur={deactivateEditMode} autoFocus='true' value={status}></input>
            :
            <span className={props.status ? ' ' : classes.profileCard__status_noStatus} onDoubleClick={activateEditMode}>{props.status ? props.status : 'no status'}</span>
            }
        </div>

        
    )
}

class ProfileStatusClass extends React.Component {
    state = { 
        editMode: false,
        status: this.props.status
    }
    activateEditMode =()=>{
        this.setState({editMode:true})
    }
    deactivateEditMode =()=>{
        this.setState({editMode:false})
        this.props.updateStatus(this.state.status)
    }
    onStatusChange = (e) => {
        this.setState({status: e.currentTarget.value})
    }
    componentDidUpdate(prevProps, prevState){
        if(this.props.status != prevProps.status)
        this.setState({status: this.props.status})
    }

    render() { 
        return ( 
            <div>
                {this.state.editMode ? 
                <input className={classes.profileCard__status_input} onChange={this.onStatusChange} onBlur={this.deactivateEditMode} autoFocus='true' value={this.state.status}></input> :
                <div className={this.props.status ? ' ' : classes.profileCard__status_noStatus} onDoubleClick={this.activateEditMode}>{this.props.status || 'no status'}</div>}
            </div>
         );
    }
}
 
export default ProfileStatus;