import React from 'react';
import { NavLink } from 'react-router-dom';
import classes from './Navbar.module.scss';

const Navbar = (props) => {
    return(
        <div className={classes.nav}>
            <div className={classes.nav__section}>
                <NavLink activeClassName={classes.active} className={classes.nav__link} to='/profile'>Profile</NavLink>
            </div>
            <div className={classes.nav__section}>
                <NavLink activeClassName={classes.active} className={classes.nav__link} to='/dialogs'>Messages</NavLink>
            </div>
            <div className={classes.nav__section}>
                <NavLink activeClassName={classes.active} className={classes.nav__link} to='/users'>Users</NavLink>
            </div>
        </div>
    )
}

export default Navbar;