// state 

import { usersAPI } from "../api/api";

let initialState = {
    users: [ ],
    pageSize: 100,
    totalCount: 100,
    currentPage: 1,
    isFetching: true,
    inProgress: [],
    portionSize: 5
};

// actions

const SET_USERS = 'SET_USERS';
const CHANGE_CURRENT_PAGE = 'CHANGE_CURRENT_PAGE';
const SET_TOTAL_USERS_COUNT = 'SET_TOTAL_USERS_COUNT';
const TOGGLE_IS_FETCHING = 'TOGGLE_IS_FETCHING';
const TOGGLE_IN_PROGRESS = 'TOGGLE_IN_PROGRESS';
const FOLLOW = 'FOLLOW';
const UNFOLLOW = 'UNFOLLOW';

export const setUsers = (users) => {return{type:SET_USERS, users}};
export const changeCurrentPage = (page) => {return{type:CHANGE_CURRENT_PAGE, page}};
export const setTotalUsersCount = (count) => {return{type:SET_TOTAL_USERS_COUNT, count}};
export const toggleIsFetching = (isFetching) => {return{type:TOGGLE_IS_FETCHING, isFetching}};
export const follow = (userId) =>{return{type: FOLLOW, userId}}
export const unfollow = (userId) =>{return{type: UNFOLLOW, userId}}
export const toggleInProgress = (inProgress, userId) => {return{type: TOGGLE_IN_PROGRESS, inProgress, userId}}
// reducer

const usersReducer = (state=initialState, action) =>{
    switch(action.type){
        case SET_USERS:
            return{...state, users: [...action.users]}
        case TOGGLE_IS_FETCHING:
            return{...state, isFetching: action.isFetching}
        case SET_TOTAL_USERS_COUNT:
            return{...state, totalCount: action.count}
        case CHANGE_CURRENT_PAGE:
            return{...state, currentPage: action.page}
        case FOLLOW:
            return{...state, users: state.users.map((user) => {if (user.id === action.userId) {return{...user, followed: true}} return user;  }
            )}
        case UNFOLLOW:
            return{...state, users: state.users.map((user) => {if (user.id === action.userId) {return{...user, followed: false}} return user;})}
        case TOGGLE_IN_PROGRESS:
            //return{...state, inProgress: action.inProgress}
            return{
            ...state, 
            inProgress: action.inProgress
                ? [...state.inProgress, action.userId] 
                : state.inProgress.filter(id => id != action.userId) 
            }
        default:
            return state;
        
    }
}

// thunks

export const getUsersTC = (currentPage, pageSize) => {
    return(dispatch) => {
        dispatch(toggleIsFetching(true));
        usersAPI.getUsers(currentPage, pageSize)
        .then((response) => {
            dispatch(toggleIsFetching(false));
            dispatch(setUsers(response.data.items));
            dispatch(setTotalUsersCount(response.data.totalCount));
            })
        }
}

export const followTC = (userId) =>{return(dispatch)=>{
    dispatch(toggleInProgress(true, userId));
    usersAPI.followUser(userId)
    .then(response => {if(response.data.resultCode ===0){
        if(response.data.resultCode === 0){dispatch(follow(userId))};
        dispatch(toggleInProgress(false, userId));
    }})
}}

export const unfollowTC = (userId) =>{return(dispatch)=>{
    dispatch(toggleInProgress(true, userId));
    usersAPI.unfollowUser(userId)
    .then(response => {if(response.data.resultCode ===0){
        if(response.data.resultCode === 0){dispatch(unfollow(userId))};
        dispatch(toggleInProgress(false, userId));
    }})
}}

export default usersReducer;