import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Redirect } from 'react-router-dom';
import { compose } from 'redux';
import withAuthRedirect from '../../hoc/AuthRedirect';
import Avatar from '../common/Avatar';
import AddNewMessage from './AddNewMessage';
import classes from './Dialogs.module.scss';
import trash from '../../assets/icons/trash-alt-regular.svg';


const Message = (props) => {
    return(
        <div className={classes.message}>
            <div className={classes.message__header}>
                <Avatar  size='small'/>
            </div>
            
            <div className={classes.message__body}>
                {props.text}
            </div>
            
            <div className={classes.message__footer}>
                <div className={classes.message__delete}>
                    <i onClick={props.deleteMessage} className='fa fa-trash-o'></i>
                </div>
            </div>
        </div>
    )
}

const Messages = (props) => {

    let messages = props.messages.map((message) => <Message id={message.id} text={message.text} deleteMessage={() => props.deleteMessage(message.id)}/>)

    let addMessage = (values) => {
        props.addMessage(values.newMessageBody)
    }
    return ( 
        <div className={classes.messages}>
            {messages}
            <AddNewMessage onSubmit={addMessage}/>
        </div> 
        );
}
 

export default compose(withAuthRedirect)(Messages);