import React from 'react';
import { Field, reduxForm } from 'redux-form';
import { maxLength, requiredField } from '../../../helpers/validations/validations';
import { Textarea } from '../../common/Textarea';
import classes from './MyPosts.module.scss';

const AddNewPostForm = (props) => {

    
    return(
        <form className={classes.form} onSubmit={props.handleSubmit}>
            <Field validate={[requiredField, maxLength]} name='newPostText' component={Textarea} placeholder='Your post...'/>
            <button className={classes.form__btn}>Add post</button>
        </form>
    )
}

const AddNewPostReduxForm = reduxForm({form: 'addNewPostForm'})(AddNewPostForm);

export default AddNewPostReduxForm;