import React from 'react';
import classes from './Avatar.module.scss';

const Avatar = (props) => { 

    let defaultAvatar = 'http://www.thisblogrules.com/wp-content/uploads/2010/02/mickey-mouse-for-facebook.jpg';

    let img = props.img ? props.img : defaultAvatar;
    
        switch(props.size){
            case('small'):
                return(
                    <div className={classes.small}>
                        <img src={img} />
                    </div>
                    ) 
            case('middle'):
                return(
                    <div className={classes.middle}>
                        <img src={img} />
                    </div>
                    ) 
            case('big'):
                return(
                    <div className={classes.big}>
                        <img src={img} />
                    </div>
                    ) 
            default:
                return(
                    <div className={classes.avatar}>
                        <img src={img} />
                    </div>
                    )
                
        }
    
}

export default Avatar; 