
import React, { Component } from 'react';
import { connect } from 'react-redux';
import {getUsersTC, setUsers, setTotalUsersCount, toggleIsFetching, changeCurrentPage, followTC, unfollowTC} from '../../redux/usersReducer';
import Avatar from '../common/Avatar';
import classes from './Users.module.scss';
import styles from './../common/Avatar.module.scss';
import Pagination from '../common/Pagination';
import Loader from '../common/Loader';
import { NavLink } from 'react-router-dom';
import FollowBtn from '../common/FollowBtn';



const User = (props) => {
    return (
        <div className={classes.userCard}>
            <div id={props.id} className={classes.userCard__header}>
                <NavLink to={'/profile/' + props.id}>
                <Avatar img={props.img} className={styles.middleAvatar} size={'middle'}/>
                </NavLink>
            </div>
            <div className={classes.userCard__body}>
                <div className={classes.userCard__name}>{props.name}</div>
                <div className={classes.userCard__status}>{props.status}</div>
            </div>
            <div className={classes.userCard__footer}>
                <div className={classes.userCard__btn}>
                    {props.followed ? 
                    <button disabled={props.inProgress.some(id => id == props.id)} onClick={props.unfollow}>Unfollow</button>:
                    <button disabled={props.inProgress.some(id => id == props.id)} onClick={props.follow}>Follow</button>}
                    <button>Message</button>
                </div>
            </div>
        </div>
    )
}

const Users = (props) => {

    let users = props.users.map((user) => <User id={user.id} img={user.photos.small}
    name={user.name} status={user.status} followed={user.followed}
    inProgress={props.inProgress}
    follow ={()=>props.follow(user.id)} 
    unfollow ={()=>props.unfollow(user.id)}/>)
    return(
        <>
            {props.isFetching ? <Loader/> :
                <>
                    <div className={classes.pagination}>
                        <Pagination {...props} selectPage={props.selectPage} />
                    </div>
                    <div className={classes.users}>
                        {users}
                    </div>  
                 </>
            }
        </>
    )
}

class UsersContainer extends Component {

    componentDidMount = () => {
        this.props.getUsersTC(this.props.currentPage, this.props.pageSize);
    }


    changePage = (page) => {
            this.props.changeCurrentPage(page);
            this.props.getUsersTC(page, this.props.pageSize);
    }

    follow = (userId) => {
        this.props.followTC(userId)
    }

    unfollow = (userId) => {
        this.props.unfollowTC(userId)
    }

    render(){
        return(
            <div>
             <Users users={this.props.users}
                    totalCount={this.props.totalCount}
                    currentPage={this.props.currentPage}
                    pageSize={this.props.pageSize}
                    isFetching={this.props.isFetching}
                    selectPage={this.changePage}
                    follow={this.follow}
                    unfollow={this.unfollow}
                    inProgress={this.props.inProgress}
                    portionSize={this.props.portionSize}
                    />
            </div>
            
        )
    }
}

const mapStateToProps = (state) => {
    return{
        users: state.usersPage.users,
        isFetching: state.usersPage.isFetching,
        pageSize: state.usersPage.pageSize,
        totalCount: state.usersPage.totalCount,
        currentPage: state.usersPage.currentPage,
        inProgress: state.usersPage.inProgress,
        portionSize: state.usersPage.portionSize
    }
}

export default connect(mapStateToProps, {getUsersTC, setUsers, toggleIsFetching, setTotalUsersCount, changeCurrentPage, followTC, unfollowTC}) (UsersContainer);
