import { profileAPI } from "../api/api";



// store

let initState = {
    profile: null,
    status: '',
    posts:[
        {
            id: 1,
            postText: 'In some ways, programming is like painting. You start with a blank canvas and certain basic raw materials. You use a combination of science, art, and craft to determine what to do with them.'
        },
        {
            id: 2,
            postText: 'The best error message is the one that never shows up.'
        }
    ]
}

// actions

let SET_PROFILE = 'SET_PROFILE';
let SET_STATUS = 'SET_STATUS';
let ADD_POST = 'ADD_POST';


export const setProfile = (profile) => {return{type: SET_PROFILE, profile}};
export const setStatus = (status) => {return{type: SET_STATUS, status}};
export const addPost = (text) => {return{type: ADD_POST, text}};

// reducer

const profileReducer = (state=initState, action) => {
    switch(action.type){
        case SET_PROFILE:
            return {...state, profile: action.profile}
        case SET_STATUS:
            return {...state, status: action.status}
        case ADD_POST:
            return {...state, posts:[{id: 3, postText: action.text}, ...state.posts]}
        default: 
            return state
    }
}

// thunks

export const getProfileTC = (userId) =>{ return (dispatch) => {
    profileAPI.getProfile(userId)
    .then(response =>{ 
        dispatch(setProfile(response.data))
    });
}}

export const updateStatusTC = (status) =>{ return (dispatch) => {
    profileAPI.updateStatus(status)
    .then(response =>{ 
        if(response.data.resultCode === 0)
        dispatch(setStatus(status))
    });
}}

export const getStatusTC = (userId) =>{ return (dispatch) => {
    profileAPI.getUserStatus(userId)
    .then(response =>{ 
        dispatch(setStatus(response.data))
    });
}}





export default profileReducer;
