// state 

let initState = {
    contacts: [
        {id: 1,
        name: 'Rami'},
        {id:2,
        name: 'Irina'},
        {id:3,
        name: 'Sergij'},
        {id: 4,
         name: 'Alya'}
    ],
    messages: [
        {id: 1,
            text: 'Hi, how are you?'},
            {id:2,
            text: 'I miss you'},
            {id:3,
            text: 'Everything is great'},
            {id: 4,
             text: 'Hahahah, lol'}
    ]
}

// actions

let ADD_MESSAGE = 'users-app/dialogs/ADD_MESSAGE';
let DELETE_MESSAGE = 'users-app/dialogs/DELETE_MESSAGE';

export const addMessage = (text) => ({type: ADD_MESSAGE, text});
export const deleteMessage = (messageId) => ({type: DELETE_MESSAGE, messageId})

// reducer

const dialogsReducer = (state = initState, action) => {
    switch(action.type){
        case ADD_MESSAGE:
            return {...state, messages: [...state.messages, {id: 5, text: action.text}]}
        
        case DELETE_MESSAGE:
            return {...state, messages: [...state.messages.filter(m => m.id != action.messageId)]}

        default: return state;
    }
}

export default dialogsReducer;