import React from 'react';
import { connect } from 'react-redux';
import Contacts from './Contacts';
import Messages from './Messages';
import classes from './Dialogs.module.scss'
import { addMessage, deleteMessage } from '../../redux/dialogsReducer';
import { compose } from 'redux';
import withAuthRedirect from '../../hoc/AuthRedirect';
import { withRouter } from 'react-router-dom';
import { getContacts, getMessages } from '../../selectors/dialogs-selectors';


const Dialogs = (props) => {
    return ( 
        <div className={classes.dialogs}>
            <Contacts contacts={props.contacts}/>
            <Messages messages={props.messages} addMessage={props.addMessage} deleteMessage={props.deleteMessage}/>
        </div>
     );
}

const mapStateToProps = (state) => {
    return{
        contacts: getContacts(state),
        messages: getMessages(state)
    }
}
 
export default compose (withAuthRedirect, connect(mapStateToProps, {addMessage, deleteMessage}))(Dialogs);