import React from 'react';
import { Field, reduxForm } from 'redux-form';
import { maxLength, requiredField } from '../../helpers/validations/validations';
import { Textarea } from '../common/Textarea';

const AddNewMessage = (props) => {
    return ( <div>
        <form onSubmit={props.handleSubmit}>
            <Field component={Textarea} placeholder='Your message...' name='newMessageBody' validate={[requiredField, maxLength]}/>
            <button>Send</button>
        </form>
    </div> );
}


 
export default reduxForm({form: 'addNewMessage'})(AddNewMessage);