import React from 'react';
import classes from './MyPosts.module.scss';
import Avatar from '../../common/Avatar.jsx';
import Loader from '../../common/Loader';

const MyPost = (props) => {
    if( !props.profile){
        return <Loader/>
    }
    return(
        <div className={classes.post}>
            <div className={classes.post__avatar}>
                <Avatar size='middle' img={props.profile.photos.small}/>
            </div>
            <div className={classes.post__text}>
                {props.postText}
            </div>
        </div>
    )
}

const MyPosts = (props) => {
    let posts = props.posts.map((post) => <MyPost postText={post.postText} profile={props.profile}/>)
    return(
        <div className={classes.posts}>
            {posts}
        </div>
    )
}

export default MyPosts;