import React from 'react';
import Avatar from "../common/Avatar";



const Contact = (props) => {
    return(
    <div>
        <Avatar size='small'/>
        {props.name}
    </div>)
}
const Contacts = (props) => {

    let contacts = props.contacts.map((contact) => <Contact id={contact.id} name={contact.name}/>)
    return ( 
        <div>
           {contacts}
        </div>
     );
}
 
export default Contacts;