import classes from './Pagination.module.scss';
import React, { useState } from 'react';

const Pagination = (props) => {

    let pagesCount = Math.ceil(props.totalCount / props.pageSize);

    let pages = [];

    for(let i = 1; i <= pagesCount; i++){
        pages.push(i)
    }

    let portionsCount = Math.ceil(pagesCount/ props.portionSize);
    let [portionNumber, setPortionNumber] = useState(1);
    let firsttNumberOfPortion = (portionNumber - 1) * props.portionSize + 1;
    let lastNumberOfPortion = portionNumber * props.portionSize;
 
    let prevPortion = () => {
        setPortionNumber(portionNumber - 1)
        
    }

    let nextPortion = () => {
        setPortionNumber(portionNumber + 1)
    }

    let pagination = pages.filter(page => page >= firsttNumberOfPortion && page <= lastNumberOfPortion).map((page) => <span onClick={() => props.selectPage(page)} className={ props.currentPage === page ? classes.pagination__btn + ' '  + classes.current : classes.pagination__btn}>{page}</span>);


    return(
        <div className={classes.pagination}>
            <button disabled={ portionNumber < 2} onClick={prevPortion} className={classes.next}>PREV</button>
            {pagination}
            <button  disabled={ portionNumber === portionsCount} onClick={nextPortion} className={classes.next}>NEXT</button>
        </div>
    )
}

export default Pagination;